import React from 'react';
import PropTypes from 'prop-types';

import injectSheet from 'react-jss';
import classNames from 'classnames';

import {
    NamespacesConsumer
} from 'react-i18next';

import languages from '../variables/languages';

import languagePickerStyle from '../assets/jss/components/languagePickerStyle';

class LanguagePicker extends React.Component {

    render() {
        const {
            classes,
            className
        } = this.props;

        return (
            <NamespacesConsumer>
                {(t, { i18n, lng }) => {
                    if (lng === 'en') {
                        i18n.changeLanguage('en-US');
                    } else if (lng === 'pt') {
                        i18n.changeLanguage('pt-BR');
                    }

                    const unselectedLanguagesKeys = Object.keys(languages).filter(key => {
                        console.log(key, lng, key.indexOf(lng))
                        return key.indexOf(lng) === -1
                    });
                    console.log(unselectedLanguagesKeys);

                    return (
                        <ul className={classNames(classes.list, className)}>
                            <li className={classes.listItem}><img src={languages[lng].flagIcon} className={classes.listItemImg} />{t(languages[lng].nameI18nKey)}</li>
                            {unselectedLanguagesKeys.map(key =>
                                <li className={classes.listItem} onClick={e => i18n.changeLanguage(key)} key={key}><img src={languages[key].flagIcon} className={classes.listItemImg} />{t(languages[key].nameI18nKey)}</li>
                            )}
                        </ul>
                    );
                }}
            </NamespacesConsumer>
        );
    }
}

LanguagePicker.propTypes = {
    classes: PropTypes.object.isRequired
};

export default injectSheet(languagePickerStyle)(LanguagePicker);
